<template>
  <div class="container">
    <div>
      <div class="card card-muni card-muni-min">
        <h3>Recuperacin de Contrasea</h3>

        <div class="alert alert-primary" v-if="siUsuarioAntiguo">Recuerde que su contrasea debe contener solo nmeros y 6 digitos como mximo.</div>

        <div :style="{display: styleDisplayCambio}">
          <div class="row justify-content-center">
            <div class="col-12 col-sm-6 col-md-12">
              <div class="mt-1 line15">
                <label>Ingrese su nueva contrasea</label>
              </div>
            </div>
            <div class="col-12 col-sm-6 col-md-12">
              <input type="password" id="nuevopass" name="nuevopass" required class="form-control input-number" v-model="nuevaClave" :maxlength="tamanioCampoPass" />
              <input type="hidden" name="hash" value />
            </div>
          </div>

          <div class="row justify-content-center mt-2">
            <div class="col-12 col-sm-6 col-md-12">
              <div class="mt-2">
                <label>Ingrese nuevamente su contrasea</label>
              </div>
            </div>
            <div class="col-12 col-sm-6 col-md-12">
              <input type="password" class="form-control input-number" id="renuevopass" name="renuevopass" required v-model="repiteClave" :maxlength="tamanioCampoPass" />
            </div>
          </div>
          <div class="row justify-content-center">
            <div class="col-12 col-sm-12 col-md-12 text-center mt-3">
              <button
                @click="CambiarPass()"
                class="btn btn-block btn-lg btn-default"
              >Crear Contrasea</button>
            </div>
          </div>
        </div>
        <div class="text-center activacion-correcta" :style="{display: styleDisplayCorrecto}">
            <div>
              <img src="../../assets/success.png" alt />
            </div>
            <h4>
              Su contrasea se ha modificado correctamente
              <br />
            </h4>
            <button
                class="btn btn-primary"
                v-bind:style="{display: styleBtnIniciarSesion}"
                @click="IniciarSesion()"
              >Iniciar Sesion</button>
          </div>
           <div class="no-existe" v-bind:style="{display: styleDisplayNoExiste}">
            <h4>Ups!</h4>
            <h5>La pagina solicitada no existe</h5>
          </div>

      </div>
    </div>
  </div>


