</template>
<script>
import axios from "axios";
import Constantes from "../../store/constantes.js";
export default {
  name: "recuperacontrasenia",
  data() {
    return {
      nuevaClave: "",
      repiteClave: "",
      styleDisplayCambio: "",
      styleDisplayCorrecto: "none",
      styleDisplayNoExiste: "none",
      styleBtnIniciarSesion: "none",
      hash: "",
      siUsuarioAntiguo:false,
      tamanioCampoPass: 20
    };
  },
  mounted() {
    this.ValidaHash();
  },
  methods: {
    ValidaFormulario() {
      var valida = true;
      if (this.nuevaClave == "") {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Debe digitar la nueva contrasea"
        });
        valida = false;
      }
      if (this.repiteClave != this.nuevaClave) {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Las contraseas no coinciden"
        });
        valida = false;
      }
      return valida;
    },
    IniciarSesion() {
      this.$router.push("/login/1");
    },
    CambiarPass() {
      if (this.ValidaFormulario()) {
        let url =`${Constantes.API_TRAMITE}/acceso/modifica-pass`;
        let usuarioPlataformaRequest = {};
        usuarioPlataformaRequest.hash = this.hash;
        usuarioPlataformaRequest.clave = this.nuevaClave;

        axios
          .post(url, usuarioPlataformaRequest)
          .then(response => {
            if (response.data.success) {
              this.$swal({
                icon: "success",
                text: "La clave fue generada con xito"
              });
              this.styleDisplayCambio = "none";
              this.styleBtnIniciarSesion = "";
              this.styleDisplayCorrecto = "";
            } else {
              this.styleDisplayNoExiste = "";
            }
          })
          .catch(error => {
            if(error.response.data.code==400){
              this.nuevaClave= "";
              this.repiteClave= "";

              this.$swal({
                icon: 'error',
                title: 'Error',
                text: 'La clave ingresada no es vlida'
              });
            }
          });
      } else {
      }
    },
    ValidaHash() {
      let hash = this.$route.params.hash;
      let proceso = this.$route.params.proceso || null;

      let url = `${Constantes.API_TAREAS_COMUN}/valida-recuperacion/${hash}`;

      axios
        .get(url)
        .then(response => {
          if (response.data.success) {
            this.hash = hash;

            if(proceso!=null && proceso=="p"){
              this.siUsuarioAntiguo = false;
              this.tamanioCampoPass = 20;
            }else if(proceso!=null && proceso=="a"){
              this.siUsuarioAntiguo = true;
              this.tamanioCampoPass = 6;
            }

          } else {
            this.styleDisplayCambio = "none";
            this.styleDisplayNoExiste = "";
          }
        }).catch(e => console.log(e));
    }
  }
};
</script>
<style lang="scss" scoped>
.no-existe {
  text-align: center;
  h4 {
    color: #21c0f3;
    font-size: 45px;
    font-weight: bold;
  }
  h5 {
    color: #21c0f3;
  }
}
.activacion-correcta {
  h4 {
    color: #21c0f3;
    padding-top: 20px;
    padding-bottom: 40px;
  }
}
</style>


